  flex-direction: column;
  flex-basis: 100%;
  justify-content: center;
  align-items: center;
  gap: .25rem;
  height: 100%;
  display: flex;
  position: relative;
}

.breadcrumbs {
  max-width: 100%;
  padding-top: .5rem;
  padding-bottom: .5rem;
  overflow-x: auto;
}

.breadcrumbs > ul, .breadcrumbs > ol {
  white-space: nowrap;
  align-items: center;
  min-height: min-content;
  display: flex;
