.breadcrumbs > ul > li, .breadcrumbs > ol > li {
  align-items: center;
  display: flex;
}

.breadcrumbs > ul > li > a, .breadcrumbs > ol > li > a {
  cursor: pointer;
  align-items: center;
  display: flex;
